.HQcontainer {
  width: 100%;
  height: calc(100% - 50px);
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.HQtopContainer {
  flex-wrap: nowrap;
  flex-shrink: 0;
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 0px 25px 20px 25px;
}
.HQpinBox {
  display: inline-block;
  min-width: 80px;
}
.HQpinUnit {
  padding: 0px 0px 10px 0px;
  min-width: 80px;
  vertical-align: middle;
  /*background-color: green;*/
}
.HQvSliderUnit {
  float: left;
  height: 190px;
  padding: 0px 0px 20px 0px;
  text-align: center;
  /*background: green;*/
}
.HQvSliderTitle {
  position: relative;
  top: -10px;
}
.HQvSliderBox {
  display: inline-block;
  min-width: 90px;
}
.HQdialBox {
  text-align: center;
  padding: 0px 0px 5px 10px;
}
.HQcaptureIconUnit {
  display: block;
  margin: auto;
  font-size: 1.1em;
  position: relative;
  top: 5px;
}
.HQcaptureButtonText {
  color: rgba(61, 153, 255, 0.75);
}
.HQpageTitle {
  color: rgba(255, 255, 255, 0.65);
  font-size: 1.5em;
  position: relative;
  top: 5px;
}
.HQtimelineBox {
  width: 100%;
  padding: 0px 20px 0px 5px;
  display: block;
  flex-wrap: nowrap;
  flex-shrink: 0;
}
.HQtimelineButtonRow {
  padding: 0px 10px 0px 30px;
  position: relative;
  top: 2px;
  flex-wrap: nowrap;
}
.HQtimelineButtonUnit {
  padding: 0px 0px 0px 10px;
}
.HQbuttonText {
  color: rgba(255, 255, 255, 0.45);
  font-size: 1em;
  /*left: -15px;*/
}
