.HQcontainer {
  width: 100%;
  height: calc(100% - 50px);
  display: flex;
  justify-content: center;
  flex-direction: column;
  // position: absolute;
  // bottom: 0px;  
  // background: @loGray;
}

@media only screen and (max-width: 600px) {

}

.HQtopContainer {
  // min-height: 200px;
  // height: calc(100vh - 450px);
  flex-wrap: nowrap;
  flex-shrink: 0; 
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  padding: 0px 25px 20px 25px; 
  // background-color: @midGray;
}

.HQpinBox {
  display: inline-block;
  min-width: 80px;
//   height: 190px;
//   background: blue;
}

.HQpinUnit {
  padding: 0px 0px 10px 0px;
  min-width: 80px;
  vertical-align: middle;
  /*background-color: green;*/
}

.HQvSliderUnit {
  float: left;
  height: 190px;
  padding: 0px 0px 20px 0px;
  text-align: center;
   /*background: green;*/
}

.HQvSliderTitle {
  position: relative;
  top: -10px;
}

.HQvSliderBox {
  display: inline-block;
//   height: 190px;
  min-width: 90px;
//   background: green;
}

.HQdialBox {
  text-align: center;
//   height: 190px;
  padding: 0px 0px 5px 10px;
//   background: yellow;
}

.HQcaptureIconUnit {
  display: block;
  margin: auto;
  font-size: 1.10em;
  position: relative;
  top: 5px;
//   background: yellow;
}

.HQcaptureButtonText {
    color: @hiBlue;
}

.HQpageTitle {
  color:@hiGray;  
  font-size: 1.5em;
  position: relative;
  top: 5px;
}

.HQtimelineBox {
  width: 100%;
  padding: 0px 20px 0px 5px;
  display: block;
  flex-wrap: nowrap;
  flex-shrink: 0; 
  // margin-top: 10px;
    // background: @primary-color;
}

.HQtimelineButtonRow {
  padding: 0px 10px 0px 30px;
  position: relative;
  top: 2px;
  flex-wrap: nowrap;
}

.HQmoSelector {
  // padding-left: 58px;
}

.HQtimelineButtonBox {

}

.HQtimelineButtonUnit {
    padding: 0px 0px 0px 10px;
    // background-color: yellow;
}

.HQbuttonText {
    color: @midGray;
    font-size: 1em;
    /*left: -15px;*/
}

